.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}

input {
  width: 500px;
  height: 50px;
  text-align: center;
  align-items: center;
  outline: none;
  border-radius: 15px;
  color: #29b0d9;
}

.search-form_input:focus {
  border: 2px solid #29b0d9;
}

.gallery {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;

  padding-left: 0px;
  margin-top: 50px;
  margin-bottom: 50px;
}

.gallery li:not(:last-child) {
  margin-right: (66px);
}

.img-thumb {
  height: 270px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
}

.container {
  width: 1400px;
  box-sizing: border-box;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.photo-card {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.51);
  width: 400px;
}
.img-thumb img {
  display: block;
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.stats {
  display: flex;
  justify-content: space-between;
}
.stats-item {
  padding-left: 10px;
  padding-right: 10px;
}
